<template>
  <b-card
    class="blog-wrapper"
  >
    <!-- form -->
    <b-form class="mt-2">
      <b-row>
        <b-col
          cols="12"
          class="mb-2"
        >
          <div class="border rounded p-2">
            <h4 class="mb-1">
              Profile Picture
            </h4>
            <validation-provider
              #default="{ errors }"
              name="Image"
              rules="required"
            >
              <b-media
                no-body
                vertical-align="center"
                class="flex-column flex-md-row"
              >
                <b-media-aside>
                  <b-avatar
                    size="120"
                    variant="light-primary"
                    :src="`${data.profile_image ? imageUrl + data.profile_image : Image}`"
                  />
                </b-media-aside>
                <b-media-body class="mt-2">
                  <small class="text-muted">Allowed JPG, JPEG, or PNG.</small>
                  <b-card-text class="my-50">
                  </b-card-text>
                  <div class="d-inline-block">
                    <b-form-file
                      v-model="data.profile_image"
                      :state="errors.length > 0 ? false:null"
                      accept=".jpg, .png, .jpeg"
                      placeholder="Choose file"
                    />
                  </div>
                </b-media-body>
              </b-media>
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </div>
        </b-col>
        <b-col md="6">
          <b-form-group
            label="Email"
            label-for="email"
            class="mb-2"
          >
            <validation-provider
              #default="{ errors }"
              name="Email"
              rules="required"
            >
              <b-form-input
                id="email"
                v-model="data.email"
                name="email"
                :state="errors.length > 0 ? false:null"
                placeholder="Type Email"
                autofocus
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group
            label="Name"
            label-for="name"
            class="mb-2"
          >
            <validation-provider
              #default="{ errors }"
              name="Name"
              rules="required"
            >
              <b-form-input
                id="name"
                v-model="data.username"
                name="name"
                :state="errors.length > 0 ? false:null"
                placeholder="Type Name"
                autofocus
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </b-col>
        <b-col
          cols="12"
          class="mt-50"
        >
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-1"
            @click="sendData"
          >
            Save Changes
          </b-button>
          <b-button
            v-ripple.400="'rgba(186, 191, 199, 0.15)'"
            variant="outline-secondary"
            :to="{ name: 'blog' }"
          >
            Cancel
          </b-button>
        </b-col>
      </b-row>
    </b-form>
    <!--/ form -->
  </b-card>
</template>

<script>
import { ValidationProvider } from 'vee-validate'
import {
  BCard, BMedia, BCardText, BMediaBody, BForm, BRow, BCol, BFormGroup, BFormInput, BFormFile, BButton, BMediaAside, BImg, BAvatar,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import { required } from '@validations'
import Image from '@/assets/images/avatars/13-small.png'

export default {
  components: {
    BCard,
    BMedia,
    BCardText,
    BMediaBody,
    BForm,
    BRow,
    BCol,
    BButton,
    BFormGroup,
    BFormInput,
    BFormFile,
    BMediaAside,
    BImg,
    BAvatar,
    ValidationProvider,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      Image,
      imageUrl: 'https://dev.api.the-netwerk.com/storage/app/',
      data: {
        username: '',
        current_password: null,
        new_password: null,
      },
      tag: [],
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    sendData() {
      const userData = JSON.parse(localStorage.getItem('userData'))
      const formData = new FormData()
      formData.append('email', userData.email)
      formData.append('username', this.data.username)
      formData.append('email_new', this.data.email)
      formData.append('image', this.data.profile_image)
      this.$swal({
        title: 'Are you sure?',
        text: 'You won\'t be able to revert this!',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, update it!',
        cancelButtonText: 'No, cancel!',
        confirmButtonClass: 'btn btn-primary',
        cancelButtonClass: 'btn btn-danger mr-1',
        buttonsStyling: false,
        reverseButtons: true,
      }).then(result => {
        if (result.value) {
          this.$http.post('updateUserByEmail', formData).then(response => {
            if (response.data.status === true) {
              const toaster = 'b-toaster-top-center'
              this.$bvToast.toast(response.data.message, {
                title: 'Notification',
                toaster,
                variant: 'success',
                solid: false,
                appendToast: false,
              })
              this.getData()
              localStorage.setItem('userData', JSON.stringify(response.data.data))
              this.$store.commit('userData/setUser', response.data.data)
            } else {
              const toaster = 'b-toaster-top-center'
              this.$bvToast.toast(response.data.message, {
                title: 'Notification',
                toaster,
                variant: 'danger',
                solid: false,
                appendToast: false,
              })
            }
          })
        } else if (
          // Read more about handling dismissals
          // eslint-disable-next-line no-undef
          result.dismiss
        ) {
          this.$swal(
            'Cancelled',
            'Your profile is safe :)',
            'error',
          )
        }
      })
    },
    getData() {
      const userData = JSON.parse(localStorage.getItem('userData'))
      const data = {
        email: userData.email,
      }
      this.$http.post('getUser', data).then(response => {
        if (response.data.status === true) {
          this.data = response.data.data
        }
      })
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
@import '@core/scss/vue/libs/quill.scss';
@import '@core/scss/vue/pages/page-blog.scss';
